.test{
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    &__box{
        width: 90%;
        border: 2px solid rgb(230, 230, 244);
        text-align: start;
         margin-top: 20px;
         padding: 10px;
        &__form{
            margin-left: 50px;
            display: flex;
            flex-direction: column;
            input{
                margin-right: 10px;
                width: 20px;
                height: 17px;
            }
        }
    }
}