.blok{
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    &__title{
        background-image: linear-gradient(90deg, #0f0e0e, #3a72ff);
        background-clip: text;
        color: transparent;
        // color: blueviolet;
    }
    &__form{
        display:flex;
        flex-direction: column;
        width: 350px;
        margin-right: auto;
        margin-left: 100px;
        &__text{
        margin-top: 20px;
        margin-bottom: 0;
        margin-right: auto;
        color: rgb(95, 95, 239);
        }
        &__select{
            
            width: 100%;
            height: 40px;
            border: 2px solid rgb(95, 95, 239);
        }
    }
}