.register{
    width: 100%;
    height: auto;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    &__form{
        border-radius: 10px;
        border: 2px solid rgb(192, 190, 190);
        display: flex;
        width: 350px;
        align-items: center;
        flex-direction: column;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        &__title{
            color: blueviolet;
            font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
            margin-right: auto;
            margin-left: 30px;
        }
        &__user{
            margin-top: 20px;
            width: 80%;
            height: 40px;
            outline: none;
            border: none;
            border-bottom: 3px solid  blueviolet;
            font-size: 18px;
        }
        &__pass{
            margin-top: 20px;
            width: 80%;
            height: 40px;
            outline: none;
            border: none;
            border-bottom: 3px solid  blueviolet;
            font-size: 18px;
        }
        &__contact{
            margin-top: 20px;
            width: 80%;
            height: 40px;
            outline: none;
            border: none;
            border-bottom: 3px solid  blueviolet;
            font-size: 18px;
        }
        &__gender{
        border-radius: 5px;
        margin-top: 40px;
        height: 40px;
        width: 60%;
        margin-right: auto;
        margin-left: 30px;
        border: 2px solid  blueviolet;
        outline: none;
        }
        &__btn{
            margin-top: 30px;
            width: 80%;
            height: 40px;
            border: 2px solid blueviolet;
            border-radius: 20px;
            background-color: white;
            color: blueviolet;
            &:hover{
                background-color: rgb(227, 219, 219);
            }
            &:active{
                border: 2px solid blue;
            }
        }
        &__link{
           margin-right: auto;
           margin-left: 30px;
           margin-top: 20px;
           margin-bottom: 20px;
           font-size: 14px;
        }
    }
}