.Login{
    width: 100%;
    height: auto;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    &__form{
        width: 350px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        
        &__title{
            color: blueviolet;
            margin-right: auto;
            margin-left: 30px;
        }
        &__user{
            width: 80%;
            height: 40px;
            border: none;
            border-bottom: 3px solid blueviolet;
            outline: none;
        }
        &__pass{
            width: 80%;
            height: 40px;
            border: none;
            border-bottom: 3px solid blueviolet;
            outline: none;
            margin-top: 30px;
        }
        &__btn{
            margin-top: 50px;
            margin-bottom: 30px;
            height: 40px;
            width: 80%;
            border-radius: 20px;
            color: blueviolet;
            border: 2px solid blueviolet;
            background-color: white;
            &:hover{
                background-color: aliceblue;
            }
            &:active{
                border: 2px solid blue;
            }
        }
    }
}